.card{
    width:45vw;
    height: 25vw;
    overflow: hidden;
    margin:5% auto;
    background-color: aquamarine;
    position: relative;
}
.imagenCard{
    width:100%;
    height:100%;
    object-fit:cover;

}
.txtCard{
    bottom: 1%;
    background-color: #fff7;
    padding: 1%;
    position:absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:5% 5%;
    font-size: 1.4vw;
}
.button{
    background-color: #a74d;
    bottom: 15%;
    border-radius: 20px;
    padding: 1%;
    position:absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:5% 5%;
    font-size: 1.4vw;
    transition: all .5s;
}
.button:hover{
    background-color: #a72;
    transform: scale(1.1);
    cursor: pointer;
}